.card{
    margin-top:10px;
   
    height: 500px;
    width: 350px;
    perspective: 1000px;
}

.card-inner{
    height: 100%;
    width: 100%;
    position: relative;
    box-shadow: 0 2px 5px rgba(250, 250, 250, 0.5);
    border-radius: 15px;
    transition: all 0.6s  ease-in;
    transform-style:preserve-3d;
}

.card:hover > .card-inner{
    cursor: pointer;
    transform: rotateY(180deg);
}

.card-front,.card-back{
    height: 100%;
    width: 100%;
    position: absolute;
    border-radius:15px;
    backface-visibility: hidden;
    transform-style:preserve-3d;
    
}

.card-front{
    background-image: url(https://studyabroad.unca.edu/wp-content/uploads/sites/61/2020/02/France_Gayter_edit-1170x512.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    align-items: center;
    justify-content: center;
}

.card-front h1{
    color: #222;
    text-shadow: 0 2px 5px #222;
    box-shadow: 0 2px 2px 2px #fff;
    border: 3px solid #222;
    padding: 0.5em;
    transform: translateZ(200px);
}


.card-front::before{
    content:'';
    position: absolute;
    top:10%;
    left:10%;
    right: 10%;
    bottom:10%;
    height: 80%;
    width:80%;
    border: 1px solid black;
    background-color: transparent;
    margin: 0 auto;
    transform: translateZ(100px);
   
}

.card-back{
    transform: rotateY(180deg);
    background-image:url(https://images.unsplash.com/photo-1478760329108-5c3ed9d495a0?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=774&q=80);
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
      
}


.card-back button{
    margin-top:45%;
    background-color: transparent;
    padding: 1em;
    padding-right: 2.5em;
    padding-left: 2.5em;
    color :teal;
    border: 2px solid teal;
    cursor: pointer;
    transform: translateZ(100px);
    
}

.card-back h1,.card-back p{
    color: teal;
    text-shadow: 0px 2px 5px teal;
    transform: translateZ(100px);
}
.card-back h1{
    transform: translateZ(150px);
}

.card-back p::before{
    content: '';
    position: absolute;
    background-color: teal;
    width: 80%;
    height: 100%;
    top:125%;
    transform: perspective(1em) rotateX(45deg) scale(1,0.2);
    filter: blur(1em);
    opacity:0;
} 

.card:hover .card-back P::before{
    opacity: 1;
}


 button::before{
  content:'';
  top: 0%;
  left: 0%;
  position: absolute;
  background-color: teal;
 
}


 .fad-border{
    margin:0;
    position: absolute;
    width: 80%;
    height: 2px;
    background-image:linear-gradient(to right, rgba(0,0,0,0),teal,rgba(0,0,0,0));
    transform: translateZ(100px);
}

.card-back button a{
    text-decoration:none;
    color: teal;
}



